body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-roboto: Roboto;
  --paragraph-2: Gotham;

  /* font sizes */
  --font-size-lg: 18px;
  --font-size-29xl: 48px;
  --font-size-5xl: 24px;
  --paragraph-2-size: 14px;

  /* Colors */
  --color-happyer-white-01: #fff;
  --gray-1: #333;
  --color-black: #000;
  --blue-2: #2d9cdb;
  --gray-3: #828282;
  --gray-6: #f2f2f2;

  /* Gaps */
  --gap-base: 16px;

  /* Paddings */
  --padding-mini: 15px;
  --padding-4xl: 23px;

  /* border radiuses */
  --br-9xs: 4px;
}
